* {
  margin: 0;
  padding: 0;
}

body {
  box-sizing: border-box;
  background-color: #e3e4e4;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
}

@media (width >= 768px) {
  body {
    padding: 2rem;
  }
}

@media (width >= 1024px) {
  body {
    padding: 2rem;
  }
}

body.dark-mode {
  background-color: #1e1e1e;
}

body.dark-mode h1, body.dark-mode h4, body.dark-mode h5, body.dark-mode ul, body.dark-mode li, body.dark-mode a {
  color: #f8f9ff;
}

body.dark-mode .overlay--close {
  background-color: #1e1e1e;
}

body.dark-mode header .navbar__link {
  color: #f8f9ff;
}

body.dark-mode header .navbar__link:hover, body.dark-mode header .navbar__link:active {
  color: #1e1e1e;
}

body.dark-mode header .navbar__img, body.dark-mode header .navbar__dark, body.dark-mode header .navbar__menu, body.dark-mode header .navbar__arrow, body.dark-mode header .hero__logo {
  filter: invert() brightness(2);
}

body.dark-mode header .hero__wrapper .header__arrow img, body.dark-mode #backToTop, body.dark-mode main .transition, body.dark-mode main .final {
  filter: invert() brightness(2);
  opacity: .75;
}

body.dark-mode main .projets-holder {
  border-top: .25px solid #f8f9ff;
}

body.dark-mode main .projets-holder > button:hover {
  border-radius: 30px;
  box-shadow: 0 1px 1px #d7c6a74d, 0 2px 2px #d7c6a74d, 0 4px 4px #d7c6a74d, 0 8px 8px #d7c6a74d, 0 16px 16px #d7c6a74d, 0 20px 20px #1e1e1e0d;
}

body.dark-mode main .about__introduction, body.dark-mode main .about__profile, body.dark-mode main .about__skills {
  border-top: .25px solid #f8f9ff;
}

body.dark-mode main .subtitle1 li, body.dark-mode main .liensHolder {
  color: #1e1e1e;
}

body.dark-mode .footer__title {
  border-bottom: .25px solid #f8f9ff;
}

body.dark-mode .footer .footer__link {
  color: #f8f9ff;
}

body.dark-mode .footer .footer__link:hover, body.dark-mode .footer .footer__link:active {
  color: #1e1e1e;
}

body.dark-mode .footer img {
  filter: invert() brightness(2);
}

#backToTop {
  opacity: 0;
  visibility: hidden;
  box-sizing: border-box;
  cursor: pointer;
  z-index: 100;
  padding: .5rem;
  transition: opacity .3s, visibility .3s;
  display: none;
  position: fixed;
  bottom: 3rem;
  right: 0;
}

#backToTop.show {
  opacity: 1;
  visibility: visible;
  display: block;
}

.hidden {
  display: none;
}

.navbar {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

@media (width >= 768px) {
  .navbar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    display: flex;
  }
}

.navbar__item {
  transition: all .5s;
}

.navbar a:link {
  color: #1e1e1e;
  padding: 1rem;
}

.navbar a:hover:not(#dark, .navbar__mode), .navbar a:active:not(#dark, .navbar__mode) {
  background-color: #d7c6a7;
  border-radius: 10px;
}

.navbar a:visited {
  color: #1e1e1e;
}

.navbar h2 {
  background-color: #d7c6a7;
}

.navbar__brand {
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1.5rem;
  padding: 1rem 0 1rem 1rem;
  display: flex;
}

.navbar__name {
