    padding-top: 5.1875rem;
  }
}

.about h2 {
  background-color: #d7c6a7;
  border-radius: 10px;
  justify-self: flex-start;
  padding: .625rem;
  display: inline-flex;
  transform: translateY(23px);
}

@media (width >= 768px) {
  .about h2 {
    transform: translateY(40px);
  }
}

.about__introduction {
  border-top: .25px solid #1e1e1e;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  padding: 2.75rem 0;
  display: flex;
}

@media (width >= 768px) {
  .about__introduction {
    grid-template-columns: repeat(2, 1fr);
    padding: 6.25rem 0;
