.about__description {
  flex-direction: column;
  order: 2;
  justify-content: space-between;
  align-items: center;
  gap: 2.5668rem;
  display: flex;
}

.about__photo {
  order: 2;
}

.about__photo img {
  object-fit: cover;
  object-position: top center;
  border-radius: 30px;
  width: 100%;
  max-width: 100%;
  height: 20rem;
  box-shadow: 0 1px 1px #00000012, 0 2px 2px #00000012, 0 4px 4px #00000012, 0 8px 8px #00000012, 0 16px 16px #00000012;
}

@media (width >= 768px) {
  .about__photo img {
    height: 26.25rem;
  }
}

.about__profile, .about__skills {
  border-top: .25px solid #1e1e1e;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
  padding: 2.75rem 0;
  display: flex;
}

@media (width >= 768px) {
  .about__profile, .about__skills {
    padding: 6.25rem 0;
  }
}

@media (width >= 1024px) {
  .about__profile, .about__skills {
    grid-template-columns: 25% 54% 21%;
    align-items: stretch;
    display: grid;
  }
}

.transition {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media (width >= 768px) {
  .transition {
    grid-template-columns: 25% 54% 21%;
    display: grid;
  }
}

@media (width >= 768px) and (width >= 1024px) {
  .transition {
    width: 1140px;
  }
}

.transition__arrow {
  opacity: 0;
  transition: opacity 3s;
  animation: 1s ease-in-out fadeInRightRotate;
  transform: rotate(52.8deg);
}

@media (width >= 768px) {
  .transition__arrow {
    grid-column-start: 2;
    padding-left: 5rem;
  }
}

.grid {
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  display: flex;
}

.grid__infos {
