  background-color: #d7c6a7;
  border-radius: 10px;
  padding: .625rem;
  display: inline-flex;
  transform: translateY(23px);
}

@media (width >= 768px) {
  .grid h2 {
    transform: translateY(40px);
  }
}

.grid__cta {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1.375rem;
  display: flex;
  transform: translateY(.5rem);
}

@media (width >= 768px) {
  .grid__cta {
    transform: translateY(1rem);
  }
}

.grid__cta i {
  font-size: 1.4rem;
}

