.projets-holder {
  box-sizing: border-box;
  border-top: .25px solid #1e1e1e;
  flex-direction: column;
  gap: 1.2rem;
  max-width: 1300px;
  padding-top: 2.75rem;
  display: flex;
}

@media (width >= 768px) {
  .projets-holder {
    aspect-ratio: 1 / 2;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "tuile1 tuile2 tuile3"
                         "tuile1 tuile2 tuile3"
                         "tuile4 tuile2 tuile5"
                         "tuile4 tuile6 tuile5"
                         "tuile4 tuile6 tuile5"
                         "tuile7 tuile8 tuile9"
                         "tuile7 tuile8 tuile9"
                         "tuile10 tuile8 tuile11"
                         "tuile10 tuile12 tuile11"
                         "tuile10 tuile12 tuile11";
    grid-auto-rows: 1fr;
    justify-content: center;
    padding-top: 8.75rem;
    display: grid;
  }
}

.projets-holder > button {
  cursor: pointer;
  border: none;
  transition: all .3s ease-in-out;
}

.projets-holder > button:hover {
  border-radius: 30px;
  box-shadow: 0 1px 1px #d7c6a74d, 0 2px 2px #d7c6a7b3, 0 4px 4px #d7c6a7b3, 0 8px 8px #d7c6a7b3, 0 16px 16px #d7c6a7b3, 0 20px 20px #1e1e1e1a;
}

.projets-holder img {
  object-fit: cover;
  border-radius: 30px;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  line-height: 0;
  display: block;
}

.dialog {
  z-index: 1000;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.modale-holder {
  z-index: 100;
  overscroll-behavior: contain;
  background-color: #f8f9ff;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

.modale-holder header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
  display: flex;
  position: relative;
}

.modale-holder header button {
  z-index: 10;
  color: #f8f9ff;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  order: 3;
  transition: all .3s ease-in-out;
  display: block;
  position: relative;
}

@media (width >= 768px) {
  .modale-holder header button {
    transform: translateX(10px);
  }
}

.modale-holder header button i {
  color: #1e1e1e;
  padding: .75rem;
  font-size: 2.8rem;
}

.modale-holder header button:hover, .modale-holder header button:focus {
  color: #1e1e1e;
  background-color: #d7c6a7;
}

.modale-holder .card-holder {
  border-radius: 30px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 1px 1px #00000012, 0 2px 2px #00000012, 0 4px 4px #00000012, 0 8px 8px #00000012, 0 16px 16px #00000012;
