    transform: translateY(0);
  }
}

.modale-holder .modale-contenu img {
  object-fit: cover;
  border-radius: 30px;
  width: 100%;
  height: 100%;
  box-shadow: 0 1px 1px #00000012, 0 2px 2px #00000012, 0 4px 4px #00000012, 0 8px 8px #00000012, 0 16px 16px #00000012;
}

@media (width >= 768px) {
  .modale-holder .modale-contenu img {
