  }
}

@media (width >= 1024px) {
  .modale-holder .modale-contenu img {
    height: 600px;
  }
}

.modale-holder .modale-contenu ul {
  flex-wrap: wrap;
  gap: .2rem;
  list-style-type: none;
  display: flex;
}

.modale-holder .modale-contenu ul li a {
